.bg {
    background-color: $bgColor;
}

.formContent {
    padding: 8px;
    max-width: 500px;
    width: 100%;

    .inputStyle {
        width: 210px;
    }
}

.spaceBetween {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .title {
        flex: 1;
    }

    .extra {
        // flex: 1;
        display: flex;
        justify-content: flex-end;

        .search {
            width: 180px;
        }
    }
}

.label {
    padding: 3px 8px;
    margin: 2px 5px;
    display: inline-block;
    background-color: #eaeaea;
    min-width: 70px;
    border-radius: 2px;
    text-align: center;
}

.scrollYBar {
    overflow-y: scroll;
    -ms-overflow-style: none;
    overflow: -moz-scrollbars-none;

    &::-webkit-scrollbar {
        width: 0 !important;
    }
}